.two-columns {
  &--about {
    .two-columns__first {
      overflow: hidden;

      img {
        @include size(100% auto);
      }
    }

    .two-columns__second {
      position: relative;
      background: $title;

      p {
        @include position(absolute, null rem(40) rem(40) null);

        font-family: 'apercu-bold';
        font-size: rem(25);
        line-height: rem(31);
        color: black;
        text-align: right;

        @include breakpoint("medium") {
          font-size: rem(30);
          line-height: rem(37);
        }
      }
    }
  }
}

.text-wrapper {
  &--about {
    @include size(100% 100vw);

    @include breakpoint("medium") {
      @include size(100% 50vw);
    }

    .text-wrapper__copy {
      font-size: rem(25);
      line-height: rem(32);
    }
  }
}

.about {

  &__wrapper {

    &__image {
      @include size(100% 25vw);
      background: url('/images/about-room.jpg') center center no-repeat;
      background-size: cover;
    }

    &__list {
      @include size(100% 100vw);
      background: $text;

      @include breakpoint("medium") {
        @include size(100% 25vw);
      }
    }

    &__slider {
      @include clearfix;

      &__item {
        float: left;
        position: relative;
        @include size(auto 25vw);
        box-sizing: border-box;

        &__content {
          @include size(100% auto);
          @include position(absolute, 50% null null null);
          @include transform(translateY(-50%));
        }

        &__title {
          padding-bottom: rem(15);

          font-family: 'apercu-bold';
          font-size: rem(26);
          color: $title;
        }

        &__desc {
          font-family: 'apercu';
          font-size: rem(15);
          line-height: rem(19);
          color: white;
        }
      }

      &--small {
        @include size(100%);

        .slick-slider {
          @include margin(0);
          @include size(100%);
        }

        .slick-list {
          @include size(100%);
        }

        .slick-dots {
          bottom: rem(40);

          li {
            @include size(rem(15) rem(20));
            @include padding(0);
            @include transition(background 0.3s ease-in);

            button {
              @include size(rem(15) rem(20));
              @include padding(0);
              @include transition(background 0.3s ease-in);

              &:before {
                content: "";
                top: rem(9);
                @include size(rem(15) rem(2));
                background: $dots;
                opacity: 1;
                @include transition(opacity 0.2s ease-in);
              }

              &:hover {
                background: white;

                &:before {
                  opacity: 0;
                }
              }
            }

            &.slick-active {
              background: white;

              button {
                &:before {
                  opacity: 0;
                  pointer-events: none;
                }
              }
            }
          }
        }

        .about__wrapper__slider__item {
          @include size(100% 100vw);

          .about__wrapper__slider__item__content {
            text-align: center;
          }
        }
      }

      &--big {
        .about__wrapper__slider__item {
          width: 25%;

          &:nth-child(1) {
            .about__wrapper__slider__item__content {
              padding-left: 33.33%;
            }
          }

          &:nth-child(2) {
            width: 50%;
            text-align: center;

            .about__wrapper__slider__item__content {
              &:before {
                content: "";
                display: block;
                @include position(absolute, 0 null null rem(60));
                @include size(rem(1) rem(98));
                background: rgba(255, 255, 255, 0.2);
              }

              &:after {
                content: "";
                display: block;
                @include position(absolute, 0 rem(60) null null);
                @include size(rem(1) rem(98));
                background: rgba(255, 255, 255, 0.2);
              }
            }
          }

          &:nth-child(3) {
            text-align: right;

            .about__wrapper__slider__item__content {
              padding-right: 33.33%;
            }
          }
        }
      }
    }
  }
}
