.team-list {
  @include breakpoint("medium") {
    @include clearfix;
  }

  .box-team {
    &:nth-child(1),
    &:nth-child(2),
    &:nth-child(5),
    &:nth-child(6),
    &:nth-child(9),
    &:nth-child(10),
    &:nth-child(13),
    &:nth-child(14) {
      @include breakpoint("medium") {
        .box-team__image {
          float: right;
        }
      }
    }
  }
}
