.footer-content {
  @include clearfix;

  @include breakpoint("medium") {
    @include padding(rem(70) null rem(70));
  }

  &__data {
    float: left;
    position: relative;
    @include size(50% 50vw);

    @include breakpoint("medium") {
      @include size(16.66% auto);
    }

    &__wrapper {
      @include position(absolute, 50% null null null);
      @include transform(translateY(-50%));

      @include breakpoint("medium") {
        @include position(static, 0 null null null);
        @include transform(translateY(0));
      }
    }

    &__title {
      @include padding(null null rem(20));

      font-family: 'apercu-bold';
      font-size: rem(23);
      color: $title;

      @include breakpoint("medium") {
        font-size: rem(21);
      }
    }

    &__text {
      @include padding(null null rem(5));

      font-family: 'apercu';
      font-size: rem(15);
      color: $text;

      @include breakpoint("medium") {
        font-size: rem(13);
      }
    }

    &--contact {
      float: none;
      clear: both;
      @include size(auto);

      @include breakpoint("medium") {
        float: left;
        clear: none;
      }

      .footer-content__data__title {
        display: none;

        @include breakpoint("medium") {
          display: block;
        }
      }

      .footer-content__data__text {
        @include padding(null null rem(1));

        a {
          display: block;
          @include size(100% rem(100));
          background: $title;

          font-family: 'apercu-bold';
          font-size: rem(23);
          line-height: rem(100);
          color: $text;
          text-align: center;

          @include breakpoint("medium") {
            @include size(auto);
            background: transparent;

            font-family: 'apercu';
            font-size: rem(13);
            line-height: inherit;
            text-align: left;
          }
        }

        @include breakpoint("medium") {
          @include padding(null null rem(5));
        }
      }
    }

    &:first-child {
      @include breakpoint("medium") {
        width: 25%;
      }

      .footer-content__data__wrapper {
        padding-left: rem(40);

        @include breakpoint("medium") {
          padding-left: 33.33%;
        }
      }
    }
  }

  &__copy {
    @include size(100% rem(100));

    text-align: center;

    @include breakpoint("medium") {
      float: right;
      @include size(auto);
      padding-right: 8.3%;
    }

    &__text {
      font-family: 'apercu';
      font-size: rem(12);
      line-height: rem(100);
      color: $text;

      @include breakpoint("medium") {
        line-height: inherit;
      }
    }
  }
}
