.main-nav {
  @include position(fixed, 0 null null 0);
  z-index: 1001;
  @include size(100%);
  background: rgba(210, 229, 252, 0.95);
  opacity: 0;
  pointer-events: none;
  @include transition(opacity 0.3s ease-in-out);

  &__wrapper {
    @include position(relative, null null null null);
    @include size(100%);
  }

  &__items {
    @include position(absolute, null rem(40) rem(40) null);

    &__link {
      @include padding(null null rem(20));
      text-align: right;

      a {
        font-family: 'apercu-bold';
        font-size: rem(20);
        color: black;
        @include transition(color 0.3s ease-in);

        &:hover {
          @include breakpoint("medium") {
            color: white;
          }
        }

        &.active {
          color: white;
        }
      }

      &:last-child {
        padding-bottom: 0;
      }
    }
  }
}

.menu-is-open {
  .main-header {
    z-index: 1002;

    &__wrapper {
      background: transparent;
    }
  }

  .main-nav {
    opacity: 1;
    pointer-events: inherit;
  }
}

.layout__homepage {
  .main-nav {
    @include breakpoint("medium") {
      width: 50%;
      left: inherit;
      right: 0;
    }
  }
}
