.content {
  position: relative;
  padding-top: rem(65);
}

.text-wrapper {
  position: relative;
  @include size(100% 100vw);
  background: white;

  @include breakpoint("medium") {
    @include size(100% 25vw);
  }

  &__copy {
    @include position(absolute, 50% null null 50%);
    @include transform(translate(-50%, -50%));
    @include size(100% auto);

    font-family: 'apercu-bold';
    font-size: rem(30);
    line-height: rem(37);
    color: black;
    text-align: center;

    @include breakpoint("medium") {
      font-size: rem(25);
      line-height: rem(32);
    }

    span {
      display: block;

      @include breakpoint("medium") {
        display: inline;
      }
    }
  }
}

.two-columns {
  @include clearfix;

  &__first {
    @include size(100% 50vh);

    @include breakpoint("medium") {
      float: left;
      @include size(50% 50vw);
    }
  }

  &__second {
    @include size(100% 50vh);

    @include breakpoint("medium") {
      float: left;
      @include size(50% 50vw);
    }
  }
}

.four-columns {
  @include clearfix;

  &__box {
    float: left;
    @include size(50% 50vw);

    @include breakpoint("medium") {
      @include size(25% 25vw);
    }

    &:nth-child(3) {
      float: right;

      @include breakpoint("medium") {
        float: left;
      }
    }
  }
}
