.main-header {
  @include position(fixed, 0 null null 0);
  z-index: 1002;
  @include size(100% rem(65));

  &__wrapper {
    @include clearfix;
    @include size(100%);
    @include padding(0 rem(40));
    background: white;
    @include transition(background 0.1s ease-in-out);
  }

  &__lg {
    float: left;
    display: block;
    @include position(relative, 50% null null null);
    @include transform(translateY(-50%));
    @include size(rem(37) rem(17));
    background: black;
  }

  &__hamb {
    float: right;
    display: block;
    @include position(relative, 50% null null null);
    @include transform(translateY(-50%));
    @include size(rem(17));

    &:before {
      content: "";
      display: block;
      position: absolute;
      @include position(absolute, rem(5) null null null);
      @include size(rem(17) rem(1));
      background: black;
      @include transition(all 0.2s ease-in);
    }

    &:after {
      content: "";
      display: block;
      @include position(absolute, rem(11) null null rem(7));
      @include size(rem(10) rem(1));
      background: black;
      @include transition(all 0.2s ease-in);
    }

    &--open {
      &:after {
        width: rem(17);
        left: 0;
        top: rem(5);
        @include transform(rotate(-45deg));
      }

      &:before {
        @include transform(rotate(45deg));
      }
    }
  }
}
