@mixin breakpoint($point, $value: 0) {
  @if $point == "small" {
    @media (min-width: 320px) { @content; }
  }

  @else if $point == "small-landscape" {
    @media (min-width: 480px) { @content; }
  }

  @else if $point == "medium-small" {
    @media (min-width: 640px) { @content; }
  }

  @else if $point == "medium" {
    @media (min-width: 768px) { @content; }
  }

  @else if $point == "medium-landscape" {
    @media (min-width: 1024px) { @content; }
  }

  @else if $point == "medium-only" {
    @media (min-width: 768px) and (max-width: 1280px) { @content; }
  }

  @else if $point == "large" {
    @media (min-width: 1280px) { @content; }
  }

  @else if $point == "extra-large" {
    @media (min-width: 1600px) { @content; }
  }

  @else {
    @media ($point: $value) { @content; }
  }
}
