.box-twitter {
  position: relative;
  @include size(100%);
  @include padding(rem(40));
  background-repeat: none;
  background-position: center;
  background-size: cover;

  &__title {
    font-family: 'apercu';
    font-size: rem(14);
    line-height: rem(17);
    color: white;

    @include breakpoint("medium-landscape") {
      font-size: rem(20);
      line-height: rem(25);
    }
  }

  &__data {
    @include position(absolute, null null rem(40) rem(40));
  }

  &__time {
    display: block;

    font-family: 'apercu';
    font-size: rem(12);
    color: white;
  }

  &__link {
    font-family: 'apercu';
    font-size: rem(12);
    color: white;
  }
}
