.herospace {
  &__main {
    position: relative;
    @include size(100% calc(100vh - 175px));
    @include padding(rem(40));
    background: white;

    @include breakpoint("medium") {
      @include size(100% calc(50vw - 50px));
    }

    &__data {
      display: none;

      &__item {
        padding-bottom: rem(5);

        font-family: 'apercu';
        font-size: rem(12);
      }

      @include breakpoint("medium") {
        display: block;
      }
    }

    &__title {
      @include position(absolute, null null rem(40) rem(40));

      font-family: 'apercu';
      font-size: rem(22);
      line-height: rem(27);

      @include breakpoint("medium") {
        bottom: rem(70);
        max-width: rem(600);
      }

      @include breakpoint("large") {
        font-size: rem(40);
        line-height: rem(50);
      }

      strong {
        font-family: 'apercu-bold';
      }
    }

    &__icon {
      display: none;
      @include position(absolute, null null rem(40) rem(40));
      @include size(rem(22) rem(10));
      background: black;

      @include breakpoint("medium") {
        display: block;
      }
    }
  }
}
