.box-team {
  @include breakpoint("medium") {
    float: left;
    @include clearfix;
    @include size(50% 25vw);
  }

  &__image {
    position: relative;
    @include size(100% 100vw);

    @include breakpoint("medium") {
      float: left;
      @include size(50% 100%);
    }

    img {
      @include size(100% auto);
    }

    &__wrapper {
      @include position(absolute, 0 null null 0);
      @include size(100%);
      background: rgba(40, 40, 40, 0.8);
      opacity: 0;
      pointer-events: none;
      @include transition(opacity 0.3s ease-in);

      &__list {
        @include position(absolute, null null rem(40) rem(40));

        &__item {
          padding-bottom: rem(5);

          a {
            font-family: 'apercu-bold';
            font-size: rem(18);
            color: white;

            @include breakpoint("medium") {
              font-size: rem(16);
            }
          }
        }
      }
    }
  }

  &__content {
    position: relative;
    @include size(100% 100vw);
    @include padding(rem(40));
    background: $text;

    @include breakpoint("medium") {
      float: left;
      @include size(50% 100%);
    }

    &__title {
      font-family: 'apercu-bold';
      font-size: rem(18);
      color: white;

      @include breakpoint("medium") {
        font-size: rem(14);
      }
    }

    &__rol {
      display: block;
      @include padding(rem(5) 0);

      font-family: 'apercu-light';
      font-size: rem(18);
      color: rgba(255, 255, 255, 0.4);

      @include breakpoint("medium") {
        font-size: rem(14);
      }
    }

    &__link {
      font-family: 'apercu';
      font-size: rem(18);
      color: white;

      @include breakpoint("medium") {
        font-size: rem(14);
      }
    }

    &__desc {
      @include position(absolute, null null rem(40) rem(40));
      @include size(60% auto);

      font-family: 'apercu-light';
      font-size: rem(16);
      line-height: rem(20);
      color: rgba(255, 255, 255, 0.4);

      @include breakpoint("medium") {
        font-size: rem(10);
        line-height: rem(12);
      }
    }
  }

  &:hover {
    .box-team__image {
      &__wrapper {
        opacity: 1;
        pointer-events: inherit;
      }
    }
  }
}
