.homepage {
  @include breakpoint("medium") {
    @include clearfix;
  }

  &__main {
    position: relative;
    @include size(100% calc(100vh - 110px));
    @include padding(rem(40));
    background: white;

    @include breakpoint("medium") {
      float: right;
      @include size(50% 100vh);
    }

    &__data {
      display: none;
      padding-top: rem(45);

      &__item {
        padding-bottom: rem(5);

        font-family: 'apercu';
        font-size: rem(12);
      }

      @include breakpoint("medium") {
        display: block;
      }
    }

    &__title {
      @include position(absolute, null null rem(40) rem(40));
      max-width: rem(250);

      font-family: 'apercu';
      font-size: rem(22);
      line-height: rem(27);

      @include breakpoint("medium") {
        bottom: rem(70);
      }

      @include breakpoint("large") {
        max-width: rem(450);

        font-size: rem(40);
        line-height: rem(50);
      }

      strong {
        display: block;

        font-family: 'apercu-bold';
      }
    }

    &__icon {
      display: none;
      @include position(absolute, null null rem(40) rem(40));
      @include size(rem(22) rem(10));
      background: black;

      @include breakpoint("medium") {
        display: block;
      }
    }
  }

  &__slider {
    position: relative;
    overflow: hidden;
    @include size(100% 100vh);

    @include breakpoint("medium") {
      float: left;
      @include size(50% 100vh);
    }

    &__images {
      @include size(100%);

      &__item {
        @include size(100%);
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
      }
    }
    img {
      @include size(auto 100%);
      max-width: inherit;

      @include breakpoint("medium") {
        @include size(100% auto);
      }
    }

    &__slides {
      @include position(absolute, null null rem(40) rem(40));

      &__item {
        display: none;

        @include breakpoint("medium") {
          display: block;
          padding-bottom: rem(8);
        }

        &:last-child {
          @include breakpoint("medium") {
            padding-bottom: rem(0);
          }
        }

        a {
          font-family: 'apercu-bold';
          font-size: rem(14);
          color: rgba(0, 0, 0, 0.3);
          @include transition(color 0.3s ease-in);

          &:hover {
            @include breakpoint("medium") {
              color: $title;
            }
          }
        }

        &--active {
          display: block;

          a {
            color: $title;
          }
        }
      }
    }

    &__actions {
      @include position(absolute, null rem(40) rem(40) null);

      &__prev {
        float: left;
        display: block;
        @include size(rem(16));

        &:before {
          content: "";
          display: block;
          @include size(rem(16));
          background: $title;
        }
      }

      &__next {
        float: left;
        display: block;
        @include size(rem(16));

        &:before {
          content: "";
          display: block;
          @include size(rem(16));
          background: $title;
        }
      }

      &__actual {
        float: left;
        @include position(relative, rem(1) null null null);
        @include padding(0 rem(15));

        font-family: 'apercu-bold';
        font-size: rem(11);
        color: white;

        span {
          display: inline-block;
          margin-left: rem(20);
          padding-left: rem(20);

          color: rgba(255, 255, 255, 0.2);

          &:before {
            content: "";
            display: block;
            @include position(relative, rem(6) null null rem(-20));
            @include size(rem(3));
            border-radius: 100%;
            background: white;
          }
        }
      }
    }
  }
}

.layout {
  &__homepage {
    .main-header {
      &__wrapper {
        @include breakpoint("medium") {
          background: transparent;
        }
      }
    }

    .content {
      padding-top: 0;
    }

    .main-header {
      @include breakpoint("medium") {
        @include size(50% rem(65));
        top: rem(10);
        left: inherit;
        right: 0;
      }
    }

    .footer-content {
      @include breakpoint("medium") {
        display: none;
      }
    }
  }
}
