.sticky-text {
  @include position(absolute, null null rem(-110) 0);
  z-index: 1001;
  @include size(100% rem(110));
  opacity: 1;
  pointer-events: none;
  @include transition(opacity 0.3s ease-in-out);

  text-align: center;

  @include breakpoint("medium") {
    display: none;
  }

  &--hide {
    opacity: 0;
  }

  &__copy {
    @include position(relative, 50% null null null);
    @include transform(translateY(-50%));
    padding-bottom: rem(25);

    font-family: 'apercu-bold';
    font-size: rem(18);
    color: white;

    &:before {
      content: "";
      display: block;
      @include size(rem(15) rem(15));
      @include position(absolute, rem(35) null null 50%);
      @include transform(translateX(-50%));
      background: white;
    }
  }
}
