.two-columns {
  &--contact {
    .two-columns__first {
      #mega-map {
        @include size(100% 50vh);
        pointer-events: none;

        @include breakpoint("medium") {
          @include size(100% 50vw);
        }
      }
    }

    .two-columns__second {
      background: url('/images/contact-image.jpg') center center no-repeat;
      background-size: cover;
    }
  }
}
