@font-face {
	font-family: 'apercu';
	src: url('/fonts/apercu/Apercu.eot');
	src: local('☺'),
  url('/fonts/apercu/Apercu.woff') format('woff'),
  url('/fonts/apercu/Apercu.ttf') format('truetype'),
  url('/fonts/apercu/Apercu.svg') format('svg');
	font-weight: normal;
	font-style: normal;
}

