.work__box {
  position: relative;
  @include size(100%);

  &__wrapper {
    display: block;
    @include size(100%);

    &:hover {
      .work__box__content {
        opacity: 1;
        pointer-events: inherit;
      }
    }
  }

  &__image {
    @include size(100%);

    img {
      @include size(100% auto);
    }
  }

  &__content {
    @include position(absolute, 0 null null 0);
    @include size(100%);
    @include padding(rem(40));
    background: rgba(212, 230, 252, 0.8);
    opacity: 0;
    pointer-events: none;
    @include transition(opacity 0.3s ease-in-out);

    &__copy {
      @include position(absolute, null null rem(40) rem(40));

      font-family: 'apercu-bold';
      font-size: rem(16);
      line-height: rem(20);

      @include breakpoint("medium") {
        font-size: rem(20);
        line-height: rem(25);
      }
    }
  }
}
